export default {
	data() {
		return {
			cover: ""
		};
	},
	onLoad(params) {
		var cover = params.cover;
		this.cover = cover;
		
		// 通过api修改导航栏的属性
		uni.setNavigationBarColor({
			frontColor: "#ffffff",
			backgroundColor: "#000000"
		});
	},
	methods: {
		operator() {
			var me = this;
			uni.showActionSheet({
				itemList: ["保存图片到本地"],
				success: function(res){
					// 下标为0则下载
					if (res.tapIndex == 0) {
						// console.log("进入下载。。。");
						uni.showLoading({
							title: "图片保存中..."
						})
						uni.downloadFile({
							url: me.cover,
							success: function(result) {
								var tempFilePath = result.tempFilePath;
								// console.log(tempFilePath);
								uni.saveImageToPhotosAlbum({
									filePath: tempFilePath,
									success: function() {
										uni.showToast({
											title: "保存成功",
											duration: 2000
										})
									},
									complete: function() {
										uni.hideLoading();
									}
								})
							}
						})
					}
				}
			})
		}
	}
}
